import * as dayjs from 'dayjs';
import { Booking } from '../../../services/data/bookings/booking.class';
import { is24HourTime } from '../../../services/data/bookings/booking.utilities';

export const DEFAULT_START_OFFSET = 120;
export const DEFAULT_DURATION = 720;
export const DEFAULT_BLOCK_SIZE = 30;
export const TIME_BLOCK_SEPARATOR_MINUTES = 5;
const DEFAULT_LOCALE_CODE = 'en';

/**
 * Representation of one block in a TimelineComponent.
 *
 * Note that 'hour' is a string type and not numeric. This is
 * for two main reasons:
 * 1. strings with a value of '0' are truthy (the number 0 isn't)
 * 2. if you need to do math with the represented time, 'value' is
 *    a much more reliable choice
 */
export interface ITimelineBlock {
    /** Block time as ms since UTC epoch */
    value: number;
    /** Whether to show block divider */
    show: boolean;
    /** Hour of the day represented by this time block (optional) */
    hour: string;
    /** Whether block is in use or not */
    in_use: boolean;
}

export interface IPanelTimelineOptions {
    /** Length of time in minutes to display on the timeline after the current time */
    duration: number;
    /** Time offset in minutes which is displayed before the current time marker */
    start_offset: number;
    /** Size of shown blocks in minutes */
    block_size: number;
    /** BCP-47 Locale for formatting time and duration */
    locale_code: string;
}

/**
 * Default set of options for {@link calculateTimelineBlocks}.
 */
export const defaultBlockOptions: IPanelTimelineOptions = {
    block_size: DEFAULT_BLOCK_SIZE,
    duration: DEFAULT_DURATION,
    locale_code: DEFAULT_LOCALE_CODE,
    start_offset: DEFAULT_START_OFFSET
};

/**
 * What to display for the optional 'hour' property of an {@link ITimelineBlock}.
 *
 * @param timestamp any time (ideally this is already rounded to the nearest minute)
 * @param options additional timeline configuration
 * @return the 'hour' part of the timestamp if its 'minute' part is 0, or else null
 */
export const hourMarker = (timestamp: dayjs.Dayjs, options: IPanelTimelineOptions): string | null => {
    let localised: string;
    if (is24HourTime(options.locale_code)) {
        // we can just use the 'hour' property directly
        localised = `${timestamp.hour()}`;
    } else {
        // clamp by 12 for 12 hour system.
        const clampedHour = timestamp.hour() % 12;
        localised = clampedHour === 0 ? '12' : '' + clampedHour;
    }

    return timestamp.minute() === 0 ? localised : null;
};

/**
 * Calculate time blocks to display
 */
export const calculateTimelineBlocks = (options?: IPanelTimelineOptions): ITimelineBlock[] => {
    if (!options) {
        options = defaultBlockOptions;
    }
    // First, round off the 'seconds' of the current time
    const nowInMinutes = dayjs().startOf('m');
    // Then round the 'minutes' part to the nearest multiple of 5
    const now = nowInMinutes.minute(Math.round(nowInMinutes.minute() / 5) * 5);
    // Define a time range:
    // 'start' is included; it's the value we attach to the earliest block
    const start = now.subtract(options.start_offset, 'm');
    // 'end' is not included; it's the boundary after the latest block
    const end = start.add(options.duration, 'm');

    const timeBlocks: ITimelineBlock[] = [];
    let timestamp = dayjs(start);
    while (timestamp.isBefore(end, 'm')) {
        timeBlocks.push({
            value: timestamp.valueOf(),
            show: (timestamp.minute() % (options.block_size)) === 0,
            hour: hourMarker(timestamp, options),
            in_use: false
        });
        timestamp = timestamp.add(TIME_BLOCK_SEPARATOR_MINUTES, 'm');
    }
    return timeBlocks;
};
/**
 * Will find and adjust each block to show if they're in use or not depending on the start and end of the bookings passed in.
 * @param timeBlocks The existing set of time blocks.
 * @param bookings The bookings to compare and find which are in use.
 * @param showInUse If false, we do not set timeBlocks status to "in_use".
 * @param options
 */
export const evaluateBookingsInUse = (
    timeBlocks: ITimelineBlock[],
    bookings: Booking[],
    showInUse: boolean,
    options?: IPanelTimelineOptions): ITimelineBlock[] => {
    const blockStart = dayjs().subtract((options ? options.start_offset : null) || DEFAULT_START_OFFSET, 'm');
    const blockEnd = dayjs().add((options ? options.duration : null) || DEFAULT_DURATION, 'm');
    // Filter bookings for the shown times
    const shownBookings = bookings.filter(i => {
        const start = dayjs(i.date);
        const end = start.add(i.duration, 'm');
        return end.isAfter(blockStart, 'm') && start.isBefore(blockEnd, 'm');
    });
    const newBlocks = [...timeBlocks];
    newBlocks.forEach(i => i.in_use = false);
    if (showInUse) {
        for (const booking of shownBookings) {
            const start = dayjs(booking.date);
            const end = start.add(booking.duration, 'm');
            for (const block of timeBlocks) {
                const blockTime = dayjs(block.value);
                if (blockTime.isSame(start, 'm') || (blockTime.isAfter(start, 'm') && blockTime.isBefore(end, 'm'))) {
                    block.in_use = true;
                }
            }
        }
    }
    return newBlocks;
};
