import { BaseDataClass } from '../base-api.class';
import { HashMap, Identity } from '../../../shared/utilities/types.utilities';
import { Booking } from '../bookings/booking.class';
import { BaseAPIService } from '../base.service';

import * as dayjs from 'dayjs';
import { rulesForSpace } from '../bookings/booking.utilities';
import { SpaceRules, SpaceRulesReason } from '../bookings/booking.interfaces';
import { ServiceManager } from '../service-manager.class';
import { Building } from '../organisation/building.class';
import { Organisation } from '../organisation/organisation.class';
import { BuildingLevel } from '../organisation/level.class';
import { User } from '../users/user.class';
import { OrganisationService } from '../organisation/organisation.service';

export interface ISpaceAvailabilityOptions {
    date?: number;
    duration: number;
    id?: string;
    room_ids?: string;
    bookable?: boolean;
    rooms?: string;
    ignore?: string;
    zone_id?: string;
    clear?: boolean;
}

export interface SpaceBookingRuleOptions {
    date?: number;
    duration?: number;
    host?: User;
}

const OPTION_DEFAULTS: ISpaceAvailabilityOptions = {
    duration: 60
};

export enum SpaceTheme {
    v1 = 'v1',
    v2 = 'v2',
}

export class Space extends BaseDataClass {
    /** Extends name for the space */
    readonly long_name: string;
    /** CSS selector for the location of the space of the level's map */
    readonly map_id: string;
    /** Type of space */
    readonly type: string;
    /** Whether user can search for this space */
    readonly can_search: boolean;
    /** Whether user can open the control URL if available */
    readonly can_control: boolean;
    /** Hourly cost for using the space */
    readonly hourly_rate: number;
    /** Whether space can be booked by users */
    readonly bookable: boolean;
    /** People capacity of the space */
    readonly capacity: number;
    /** Index to force order when sorting multiple spaces */
    readonly sort_priority: number;
    /** Setup time for bookings in this space */
    readonly setup: number;
    /** Breakdown time for bookings in this space */
    readonly breakdown: number;
    /** Last returned availability result */
    readonly was_available: boolean;
    /** The version of the UI to support multiple version theming. */
    private readonly _theme_rbp: SpaceTheme;
    /** A BCP-47 code specifying what language to use in the UI. */
    private readonly _locale_code: string | undefined;
    /** Bookings associated with the space */
    private _bookings: string[];
    /** Engine zones associated with the space */
    public readonly zones: string[];
    /** List of equipment available in the space */
    private _extras: Identity[];
    /** List of ids for linked spaces */
    private _linked_spaces: string[];

    constructor(protected service: BaseAPIService<Space>, raw_data: HashMap = {}) {
        super(service, raw_data);
        const settings = raw_data.settings || {};
        this.zones = raw_data.zones instanceof Array ? raw_data.zones : [];
        this._theme_rbp = settings.theme_rbp || raw_data.theme_rbp || (this.building.setting('theme_rpb'));
        this._locale_code = raw_data.locale_code || this.building.setting('locale_code');
        this.long_name = settings.long_name || raw_data.long_name;
        this.map_id = settings.map_id || raw_data.map_id;
        this.type =
            settings.book_type ||
            (this.level?.settings || {}).book_type ||
            raw_data.book_type ||
            raw_data.type ||
            'book';
        this.can_search = settings.searchable || raw_data.searchable || raw_data.can_search || false;
        this.can_control = settings.controlable || raw_data.controlable || raw_data.can_control || false;
        this.hourly_rate = settings.cost_hour || raw_data.cost_hour || raw_data.hourly_rate || 0;
        this.bookable = settings.bookable || raw_data.bookable || false;
        this.setup = ((settings.setup || 0) / 60) || raw_data.setup || 0;
        this.breakdown = ((settings.breakdown || 0) / 60) || raw_data.breakdown || 0;
        this.capacity = raw_data.capacity || 0;
        this.was_available = settings.available || raw_data.available || raw_data.was_available || false;

        const raw_bookings = settings.bookings || raw_data.bookings || [];
        this._bookings = raw_bookings.map(i => i.id);
        if (this.service.parent && this.service.parent.Bookings) {
            if (raw_bookings) {
                const id_list = [];
                raw_bookings.forEach(booking => {
                    let id = booking.id;
                    this.service.parent.Bookings.removeFrom(this.id, [id], 'class');
                    id = this.service.parent.Bookings.addFrom(this.id, booking, 'class');
                    id_list.push(id);
                });
                this._bookings = id_list;
            }
        }
        this._extras = [];
        this._linked_spaces = settings.linked_rooms || raw_data.linked_rooms || raw_data.linked_spaces || [];

        // Do this last, after the bookings have been setup
        this.update_bookings_display(this.locale_code);
    }

    /** Whether the space has a booking in progress */
    public get in_use(): boolean {
        return !!this.current;
    }

    /** Get any current in progress meeting */
    public get current(): Booking | null {
        const bookings = this.bookings;
        for (const bkn of bookings) {
            const status = bkn.status;
            if (status === 'in_progress' || status === 'started') {
                return bkn;
            }
        }
        return null;
    }

    /** Get the next upcoming meeting */
    public get next(): Booking {
        const now = dayjs().valueOf();
        const bookings = this.bookings;
        for (const bkn of bookings) {
            if (bkn.date > now) {
                return bkn;
            }
        }
        return null;
    }

    /** Retrieve a BCP-47 tag representing the default locale (language, currency, etc.) of this space */
    public get locale_code(): string {
        // Default to 'en' (we localise most spaces to English (and only English) at this time)
        return this._locale_code || this.building.setting('locale_code') || 'en';
    }

    public get theme_rbp(): SpaceTheme | null {
        return this._theme_rbp || this.building.setting('theme_rbp') || 'v1';
    }

    /** List of stored bookings for the space */
    public get bookings(): Booking[] {
        const bookingService = ServiceManager.serviceFor(Booking);
        const booking_list = (bookingService
            && bookingService.list((booking) => this._bookings.indexOf(booking.id) >= 0) || []);
        return booking_list.sort((a, b) => a.date - b.date);
    }

    /** List of bookings for today */
    public get todays_bookings(): Booking[] {
        return this.bookingsFor(dayjs().valueOf());
    }

    /** List of ids for linked spaces */
    public get linked_spaces() {
        return [...this._linked_spaces];
    }

    /** Level in which the space is associated */
    public get level(): BuildingLevel | null {
        const service: OrganisationService = ServiceManager.serviceFor(Organisation) as OrganisationService;
        return service ? service.levelWithID(this.zones as any) : null || new BuildingLevel({});
    }

    public get building(): Building {
        const service = ServiceManager.serviceFor(Organisation) as OrganisationService;
        const building = service ? service.buildingWithID(this.zones as any) : null;
        return building || new Building(service as any, {});
    }

    /**
     * Get bookings for space
     * @param date Date to filter bookings on
     */
    public bookingsFor(date?: number): Booking[] {
        if (date) {
            const day = dayjs(date);
            return this.bookings.filter(i => dayjs(i.date).isSame(day, 'd'));
        } else {
            return this.bookings;
        }
    }

    /**
     * Whether the space is available.
     */
    public isAvailable(options?: ISpaceAvailabilityOptions): Promise<boolean> {
        options = options
            ? { ...OPTION_DEFAULTS, ...options, room_ids: this.id }
            : { ...OPTION_DEFAULTS, room_ids: this.id };
        return new Promise((resolve) => {
            (this.service as any).available(options).then((list: Space[]) => {
                for (const rm of list) {
                    if (rm.id === this.id) {
                        return resolve(true);
                    }
                }
                resolve(false);
            }, () => resolve(false));
        });
    }

    /**
     * Make a copy of this object
     */
    public clone(): Space {
        return new Space(this.service, this);
    }

    /**
     * Make a copy of this object without identification data
     */
    public duplicate(): Space {
        return new Space(this.service, { ...this, id: null, email: null, _bookings: [] });
    }

    /**
     * Generate the booking rules for space with given options
     * @param options Conditions for generating the space rules
     */
    public rulesFor(options: SpaceBookingRuleOptions): SpaceRules {
        const service = ServiceManager.serviceFor(Building);
        if (!service || !this.level) {
            return {
                auto_approve: true,
                hide: false,
                reason: SpaceRulesReason.NoBuildingService,
                failures: [!this.level ? 'No Level' : 'No Service']
            };
        }
        const building = service.buildings.find((bld) => bld.id === this.level.building_id);
        if (!building) {
            return { auto_approve: true, hide: false, reason: SpaceRulesReason.BuildingNotFound };
        }
        const { date, duration, host } = options;
        return rulesForSpace({
            time: date,
            duration,
            space: this,
            user: host,
            rules: building.booking_rules
        });
    }

    /**
     * Whether space can only be booked by request
     * @param options Conditions for checking the space rules
     */
    public byRequest(options: SpaceBookingRuleOptions) {
        const rules = this.rulesFor(options);
        return !rules.auto_approve;
    }

    /*
     * Invoke {@link Booking.update_display} on each of the contained Bookings.
     *
     * In some test scenarios, bookings don't have an 'update_display' function.
     * See <code>generateMockBooking()</code> in booking.utilities.ts for more
     * details.
     */
    public update_bookings_display(localeCode: string) {
        for (const booking of this.bookings) {
            if (booking instanceof Booking) {
                booking.update_display(localeCode);
            }
        }
    }
}
