import { Component, Input } from '@angular/core';
import { Space } from '../../../services/data/spaces/space.class';

/**
 * The 'details' section of a {@link BookingPanelComponent}.
 *
 * NB: A BookingPanelComponent or equivalent ancestor component is responsible
 * for ensuring that the 'space' property and all ngx-translate
 * <code>TranslateService</code> properties are set properly.
 */
@Component({
    selector: 'app-panel-space-details',
    template: `
        <div class="space-booking-details">
            <div class="booking-container">
                <div class="details current" *ngIf="space && space.current">
                    <div class="heading">{{ 'space-details.booking.current.heading' | translate }}</div>
                    <div class="host" *ngIf="!hide_user && !hide_details">{{ space.current?.organiser?.name }}</div>
                    <div class="time" *ngIf="!hide_availability">
                        {{ 'space-details.booking.current.time-range' | translate:{
                            time: space.current?.end_time,
                            date: space.current?.end_date
                        } }}
                    </div>
                </div>
                <div class="details next" *ngIf="space && space.next">
                    <div class="heading">{{ 'space-details.booking.next.heading' | translate }}</div>
                    <div class="host" *ngIf="!hide_user && !hide_details">{{ space.next?.organiser?.name }}</div>
                    <div class="time" *ngIf="!hide_availability">
                        {{ 'space-details.booking.next.time-range' | translate:{
                            time: space.next?.start_time,
                            date: space.next?.start_date
                        } }}
                    </div>
                </div>
            </div>
            <div class="details other">
                <div class="time" *ngIf="!hide_availability && space?.current"
                    [innerHTML]="'space-details.other.availability' | translate:{
                        time: this.space.current.end_time,
                        date: this.space.current.end_date
                    }">
                </div>
                <div class="time"
                    [innerHTML]="'space-details.other.link' | translate">
                </div>
            </div>
            <div class="information" *ngIf="title || description">
                <div class="title">{{ title }}</div>
                <div class="block">
                    <div class="icon" *ngIf="icon">
                        <img [src]="icon">
                    </div>
                    <div class="text">{{ description }}</div>
                </div>
            </div>
            <div class="bindings" *ngIf="space">
                <i binding [sys]="space.id" mod="Bookings" bind="hide_details" [(model)]="hide_details"></i>
                <i binding [sys]="space.id" mod="Bookings" bind="hide_title" [(model)]="hide_title"></i>
                <i binding [sys]="space.id" mod="Bookings" bind="hide_availability" [(model)]="hide_availability"></i>
                <i binding [sys]="space.id" mod="Bookings" bind="hide_user" [(model)]="hide_user"></i>
                <i binding [sys]="space.id" mod="Bookings" bind="title" [(model)]="title"></i>
                <i binding [sys]="space.id" mod="Bookings" bind="description" [(model)]="description"></i>
                <i binding [sys]="space.id" mod="Bookings" bind="icon" [(model)]="icon"></i>
            </div>
        </div>
    `,
    styleUrls: ['./space-details.component.scss']
})
export class PanelSpaceDetailsComponent {
    /** Space with which to display bookings for */
    @Input() public space: Space;
    /** Secondary title for the space */
    public title: string;
    /** Description for the space */
    public description: string;
    /** Image that suppliments the description for the space */
    public icon: { class?: string, value?: string, src?: string };

    /** Whether to hide the booking timing details */
    public hide_availability: boolean;
    /** Whether to hide the current booking title */
    public hide_title: boolean;
    /** Whether to hide booking details(title and host) */
    public hide_details: boolean;
    /** Whether to hide booking host */
    public hide_user: boolean;

}

