import { Component } from '@angular/core';
import { DebugService } from './services/debug.service';

@Component({
    selector: 'debug-outlet',
    template: `
        <div class="toggle-space" (click)="show = !show"></div>
        <div class="debug-outlet" *ngIf="show">
            <h4>Debug logs</h4>
            <div class="list">
                <div class="log" *ngFor="let log of log_list | async">
                    {{ log.time | date:'HH:mm:ss' }} <span>[{{log.level}}]</span> {{ log.content | json }}
                </div>
            </div>
        </div>
    `,
    styles: [`
        .toggle-space {
            position: absolute;
            top: 0;
            left: 0;
            height: 2em;
            width: 2em;
            z-index: 9999;
        }

        .debug-outlet {
            position: absolute;
            bottom: .5em;
            left: .5em;
            background-color: #fff;
            border-radius: 4px;
            z-index: 999;
            box-shadow: 0 1px 3px 1px rgba(0,0,0, .2),
                        0 1px 1px 0 rgba(0,0,0, .14),
                        0 2px 1px -1px rgba(0,0,0, .12);
        }

        h4 { width: 100%; border-bottom: 1px solid #ccc; margin: 0; padding: .5rem }

        .log {
            font-size: .7em;
            font-family: monospace;
            padding: .5em;
            white-space: nowrap;
        }

        .log:nth-child(2n) {
            background-color: rgba(0,0,0,0.05);
        }

        span {
            font-family: monospace;
            opacity: .8;
        }

        .list {
            max-height: 20em;
            max-width: 40em;
            overflow: auto;
        }
    `]
})
export class DebugOutletComponent {

    public show: boolean = false;

    public get log_list() {
        return this._debug.logs;
    }

    constructor(private _debug: DebugService) {}
}
