import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { map } from 'rxjs/operators';

export interface DebugLog {
    /** Logging level of the message */
    level: 'log' | 'info' | 'warn' | 'debug' | 'error';
    /** Contents of the log message */
    content: any;
    /** Time that the message was logged */
    time: number;
}

@Injectable({
    providedIn: 'root',
})
export class DebugService {

    private _old_log = console.log;
    private _old_info = console.info;
    private _old_warn = console.warn;
    private _old_debug = console.debug;
    private _old_error = console.error;

    /** Store for list of logs */
    private _logs = new BehaviorSubject<DebugLog[]>([]);
    /** Observable for list of logs */
    public readonly logs = this._logs.pipe(map(list => list.reverse()));

    constructor() {
        console.log = (...args: any[]) => {
            this.newLogMessage('log', args);
            this._old_log.call(this, ...args);
        };

        console.info = (...args: any[]) => {
            this.newLogMessage('info', args);
            this._old_info.call(this, ...args);
        };

        console.warn = (...args: any[]) => {
            this.newLogMessage('warn', args);
            this._old_warn.call(this, ...args);
        };

        console.debug = (...args: any[]) => {
            this.newLogMessage('debug', args);
            this._old_debug.call(this, ...args);
        };

        console.error = (...args: any[]) => {
            this.newLogMessage('error', args);
            this._old_error.call(this, ...args);
        };
    }

    private newLogMessage(level, content) {
        const logs = [...this._logs.getValue()];
        logs.push({ level, content, time: new Date().valueOf() });
        logs.sort((a, b) => a.time - b.time);
        this._logs.next(logs);
    }
}
