import { Space } from '../spaces/space.class';
import { User } from '../users/user.class';
import { HashMap } from 'src/app/shared/utilities/types.utilities';

export interface IBookingOptions {
    space: Space;
    date?: number;
    title?: string;
    max_duration?: number;
    min_duration?: number;
}

export interface BookingNote {
    /** Type of note */
    type: 'equipment' | 'catering' | 'description' | 'private' | 'other';
    /** Name of the note's author */
    author: string;
    /** Contents of the note */
    message: string;
    /** Time the note was added to the booking */
    date: number;
    /** Display value for the time */
    time?: string;
    /** ID of the space associated with the note */
    space?: string;
    /** ID of the catering order associated with the note */
    order_id?: string;
}

/** Details about recurrence associated with the booking */
export interface BookingRecurrenceDetails {
    /** Period of recurrence */
    readonly period: string;
    /** Unix epoch in seconds of the recurrence start date */
    readonly start: number;
    /** Unix epoch in seconds of the recurrence end date */
    readonly end: number;
    /** Calendar UID of the initial event */
    readonly series_id: string;
    /** List of replacement spaces for unavailable events in the original spaces */
    readonly conflicts: BookingRecurrenceSpaceDetails[];
}

export interface BookingRecurrenceSpaceDetails {
    /** Date of the recurrence event */
    date: number;
    /** Email address of the space the event is occurring */
    space: string;
    /** Email address of the space replaced for this occurrence */
    replaces?: string;
}

export enum SpaceRulesReason {
    NoBuildingService = 'NoBuildingService',
    BuildingNotFound = 'BuildingNotFound',
    NoMatchingRules = 'NoMatchingRules',
    MatchesRules = 'MatchesRules',
}

export interface SpaceRules {
    auto_approve: boolean;
    hide: boolean;
    max_length?: number;
    min_length?: number;
    reason?: SpaceRulesReason;
    // list of checks that failed
    failures?: string[];
}

export interface BookingRule {
    conditions: BookingRuleConditions;
    rules: {
        book_ahead?: string;
        book_length?: string;
        auto_approve?: boolean;
    };
}

export interface BookingRuleConditions {
    /** List of user groups that the host must be in */
    groups?: string[];
    /** List of locations that the host must be */
    locations?: string[],
    /** Minimum length of a booking */
    min_length?: string;
    /** Maximum length of a booking */
    max_length?: string;
    /** How far in the future this bookings are allowed */
    is_before?: string;
    /** How far in the future this bookings must be */
    is_after?: string;
}

export interface SpaceRuleOptions {
    user?: User;
    space: Space;
    time?: number;
    recurr_end?: number;
    duration?: number;
    rules?: HashMap<readonly BookingRule[]>;
}

export interface SpaceCheckOptions {
    user?: User;
    space: Space;
    time?: number;
    recurr_end?: number;
    duration?: number;
    rules: BookingRuleConditions;
}
