import { HttpClient } from '@angular/common/http';
import { FactoryProvider } from '@angular/core';

import { TranslateLoader, TranslateModuleConfig } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';

import { Observable, of } from 'rxjs';


// Kudos to the guide in https://github.com/ngx-translate/http-loader/blob/0409e32f75/README.md for syntax.
const standardLoader: FactoryProvider = {
    provide: TranslateLoader,
    useFactory: client => new TranslateHttpLoader(client, "assets/i18n/"),
    deps: [HttpClient]
};

/**
 * A more configurable alternative to {@link standardLoader}.
 *
 * In production builds this is not the best choice. In tests, though,
 * being able to supply alternative translation objects is quite helpful.
 *
 * @param translations a map from BCP 47 language codes to arbitrary objects
 */
function bundledProvider(translations: Map<string, any>): FactoryProvider {
    const bundledLoader: TranslateLoader = {
        getTranslation(lang: string): Observable<any> {
            return of(translations.get(lang));
        }
    };
    return {
        provide: TranslateLoader,
        useFactory: () => bundledLoader,
        deps: []
    };
}

/**
 * For use with 'TranslateModule.forRoot()' calls.
 *
 * This returns a config whose loader conforms to the 'FactoryProvider' interface.
 */
export const translateModuleConfig: TranslateModuleConfig = {
    loader: standardLoader
};

/**
 * This config is (currently) only approved for test purposes.
 *
 * Production builds should prefer {@link translateModuleConfig} instead.
 *
 * @param translations map of BCP 47 language-codes to translation JSON
 */
export function bundledTranslateConfig(translations: Map<string, any>): TranslateModuleConfig {
    return {
        loader: bundledProvider(translations)
    };
}
