import { Component, Input, ViewEncapsulation } from '@angular/core';
import { Space } from '../../services/data/spaces/space.class';
import { BaseClass } from '../../shared/base.class';

@Component({
    selector: 'app-panel-details-display',
    template: `
        <div class="content">
            <span class="panel-title" (tapped)="showTitle()" [class.show]="show_title">{{ space_name }}</span>
            <div class="details" *ngIf="!hide_all && show_details">
                <app-panel-space-details [space]="space"></app-panel-space-details>
            </div>
        </div>
    `,
    styleUrls: ['./panel.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class PanelDetailsDisplayComponent extends BaseClass {
    @Input()
    show_title: boolean;

    @Input()
    space_name: string;

    @Input()
    space: Space;

    @Input()
    show_details: boolean;

    @Input()
    hide_all: boolean;

    /**
     * Show full room name if overflowing
     */
    public showTitle() {
        this.show_title = true;
        this.timeout('hide_title', () => (this.show_title = false), 3000);
    }
}
