import { HashMap } from '../../../shared/utilities/types.utilities';
import { generateMockBooking } from '../bookings/booking.utilities';

import * as faker from 'faker';
import { USER_DOMAIN } from '../users/user.utilities';
import { predictableRandomInt } from '../../../shared/utilities/general.utilities';

let SPACE_COUNT: number = 0;

export function generateMockSpace(overrides: HashMap = {}): HashMap {
    const id = `space-${SPACE_COUNT++}`;
    const name = `${faker.name.firstName()} ${faker.name.lastName()} Space`;
    const linked = Math.floor(predictableRandomInt(99999)) % 2 === 0 && SPACE_COUNT > 1;
    return {
        id,
        name,
        long_name: `${name} with an long name`,
        map_id: `${SPACE_COUNT}`,
        capacity: Math.floor(predictableRandomInt(20) + 1) * 2,
        email: `${name.toLowerCase().split(' ').join('.')}@${USER_DOMAIN}`,
        type: faker.commerce.productName(),
        searchable: Math.floor(predictableRandomInt(99999)) % 2 === 0,
        controlable: Math.floor(predictableRandomInt(99999)) % 2 === 0,
        bookable: Math.floor(predictableRandomInt(99999)) % 2 === 0,
        cost_hour: Math.floor(predictableRandomInt(300)) * 100,
        setup: Math.floor(predictableRandomInt(6)) * 5,
        breakdown: Math.floor(predictableRandomInt(6)) * 5,
        zones: ['zone_lvl-0'],
        support_url: `/control/#/${id}`,
        bookings: Array(10).fill(0).map(i => generateMockBooking()),
        linked_rooms: linked ? [`space-${Math.floor(predictableRandomInt(SPACE_COUNT - 1))}`] : [],
        image: faker.image.business(),
        ...overrides
    };
}
