import { Component, Input, ViewEncapsulation } from '@angular/core';
import { Space } from '../../../services/data/spaces/space.class';
import { BaseClass } from '../../../shared/base.class';

@Component({
    selector: 'app-details-display-1',
    template: `
        <div class="content">
            <div class="title" (tapped)="showTitle()" [class.show]="show_title">{{ space_name }}</div>
            <div [class]="'status-text' + (status && !hide_all ? ' ' + status : '')">
                {{ (!hide_all ? status_display : null) || 'Not Bookable' }}
            </div>
            <div class="details" *ngIf="!hide_all && show_details">
                <panel-space-details [space]="space"></panel-space-details>
            </div>
        </div>
    `,
    styleUrls: ['./panel-details-display-v1.component.scss'],
    encapsulation: ViewEncapsulation.None,
})
export class PanelDetailsDisplayV1Component extends BaseClass {

    @Input()
    show_title: boolean;

    @Input()
    space_name: string;

    @Input()
    status: string;

    @Input()
    space: Space;

    @Input()
    show_details: boolean;

    @Input()
    hide_all: boolean;

    @Input()
    status_display: string;

    /**
     * Show full room name if overflowing
     */
    public showTitle() {
        this.show_title = true;
        this.timeout('hide_title', () => (this.show_title = false), 3000);
    }
}
