import { Component, Input, OnInit } from '@angular/core';
import { BaseComponent } from '../../shared/base.component';
import { Space, SpaceTheme } from '../../services/data/spaces/space.class';
import { HashMap } from '../../shared/utilities/types.utilities';
import * as dayjs from 'dayjs';
import { ApplicationService } from '../../services/app.service';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { timeToDate } from '../../shared/utilities/general.utilities';
import { catchError, map, mergeMap } from 'rxjs/operators';
import { SpaceRules } from '../../services/data/bookings/booking.interfaces';
import { BehaviorSubject, combineLatest, concat, from, Observable, of } from 'rxjs';
import { defaultBlockOptions, IPanelTimelineOptions } from './timeline/timeline.utils';
import { is24HourTime } from '../../services/data/bookings/booking.utilities';
import { Building } from 'src/app/services/data/organisation/building.class';

/**
 * The accepted states for a given room or space.
 *
 * Extension to/reinterpretation of {@link PermittedSystemStatus}.
 *
 * These constants are used as CSS classes and as keys in the /assets/i18n
 * directory - please take care when changing them.
 */
export enum RoomDisplayStatus {
    Available = 'available',
    NotBookable = 'not-bookable',
    AvailableByRequest = 'available-by-request',
    InUse = 'in-use',
    Pending = 'none'
}

/**
 * Expected states for an enabled room or space.
 *
 * These constants may be different from those exported as part of
 * {@link RoomDisplayStatus}.
 *
 * Only use this type inside BookingPanelComponent. Please be careful to use
 * the right constant in the right place.
 */
type EnabledSystemStatus = 'pending' | 'available' | 'unavailable';

/**
 * Extension to {@link EnabledSystemStatus}.
 *
 * In addition to the types listed there, this adds 'not-bookable' to designate
 * rooms/spaces that want to pretend to be offline.
 */
type PermittedSystemStatus = EnabledSystemStatus | 'not-bookable';


@Component({
    selector: 'app-booking-panel',
    template: `
        <div class="booking-panel" [class.disabled]="interactive === false" [class.reverse]="reverse || flip_status"
             [class.no-time]="!show_time">
            <div *ngIf="isV1() === true"
                 [class]="'status-v1' + (status && !hide_all ? ' ' + status : '')">
                <div class="time-display-v1" *ngIf="show_time">{{ display_time | async }}</div>
            </div>
            <div *ngIf="isV1() === false"
                 [class]="'status' + (normalisedStatus && !hide_all ? ' ' + normalisedStatus : '')">
                <span class="time-display" *ngIf="show_time">{{ userFacingStatusText | async }}</span>
                <span class="time-display time-display-bottom" *ngIf="show_time">{{ display_time | async }}</span>
            </div>
            <div class="body">
                <app-panel-details-display
                        *ngIf="isV1() === false"
                        class="details-display"
                        [hide_all]="hide_all" [show_details]="show_details" [show_title]="show_title"
                        [space]="space" [space_name]="space_name"></app-panel-details-display>
                <app-details-display-1
                        *ngIf="isV1() === true"
                        class="details-display-v1"
                        [hide_all]="hide_all" [show_details]="show_details" [show_title]="show_title"
                        [space]="space" [space_name]="space_name" [status]="status"
                        status_display="{{ status_display | async }}"></app-details-display-1>
                <div class="timeline" *ngIf="show_details">
                    <panel-timeline [space]="space" [options]="timeline_options"></panel-timeline>
                </div>
                <div class="loading-block" *ngIf="isV1() === undefined">
                    <div class="info-block">
                        <div *ngIf="!system_id" class="title">Starting Application...</div>
                        <div *ngIf="system_id && !space_name" class="title">System ({{system_id}}) found...</div>
                        <div *ngIf="space_name" class="title">{{space_name}} Panel connecting...</div>
                        <div *ngIf="system_id && !space_name" class="text">Loading Panel settings...</div>
                        <div *ngIf="space_name" class="text">Applying Panel settings...</div>
                        <div class="icon">
                            <a-spinner ring-rotate color="#000"></a-spinner>
                        </div>
                    </div>
                </div>
            </div>
            
            <div class="bindings" *ngIf="system_id">
                <i binding [sys]="system_id" mod="Bookings" bind="room_name" [(model)]="space_name"></i>
                <i binding [sys]="system_id" mod="Bookings" [(model)]="interactive"></i>
                <i binding [sys]="system_id" mod="Bookings" bind="today" (modelChange)="updateBookings($event)"></i>
                <i binding [sys]="system_id" mod="Bookings" bind="timeout" [(model)]="pending_timeout"
                   (modelChange)="updateStatus()"></i>
                <i binding [sys]="system_id" mod="Bookings" bind="cancel_timeout" [(model)]="cancel_timeout"
                   (modelChange)="updateStatus()"></i>
                <i binding [sys]="system_id" mod="Bookings" bind="last_meeting_started" [(model)]="last_started"
                   (modelChange)="updateStatus()"></i>
                <i binding [sys]="system_id" mod="Bookings" bind="hide_all" [(model)]="hide_all"></i>
                <i binding [sys]="system_id" mod="Bookings" bind="flip_status" [(model)]="flip_status"></i>
                <i binding [sys]="system_id" mod="Bookings" bind="can_cancel" [(model)]="can_cancel"></i>
                <i binding [sys]="system_id" mod="Bookings" bind="business_start" [(model)]="business_start"></i>
                <i binding [sys]="system_id" mod="Bookings" bind="business_end" [(model)]="business_end"></i>
                <i binding [sys]="system_id" mod="Bookings" bind="status_override" [(model)]="status_overrides"></i>
            </div>
        </div>
    `,
    styleUrls: ['./panel.component.scss']
})
export class BookingPanelComponent extends BaseComponent implements OnInit {
    /** ID of the active system */
    @Input() public system_id: string;
    /** Whether the status is on the right side */
    @Input() public reverse: boolean;
    /** Whether to show the time at the bottom of the status */
    @Input() public show_time = true;
    /** Name of the connected system */
    public space_name: string;
    /** Space with which to display bookings for. lazy loaded */
    public space: Space | undefined;
    public rules: SpaceRules | undefined;
    /** Number of seconds before a meeting can be started, being truthy enables start/stop and auto-cancellations */
    public pending_timeout: number;
    /** Number of seconds after the start of a meeting when it is auto-cancelled, overrides `timeout` value */
    public cancel_timeout: number;
    /** Timestamp of the last started meeting as ms since UTC epoch */
    public last_started: number;
    /** Whether to hide booking related details */
    public hide_all: boolean;
    /** Whether user interaction is enabled for the panel */
    public interactive = true;
    /** Whether status display is on the right side of the UI */
    public flip_status: boolean;
    /** Whether bookings can be cancelled if they timeout */
    public can_cancel: boolean;
    /** Time of the last cancelled meeting in ms since UTC epoch */
    private last_cancel: number;
    /** Start time of the business day with format `HH:mm` */
    public business_start: string;
    /** End time of the business day with format `HH:mm` */
    public business_end: string;
    /** Whether to show full room name when it overflows */
    public show_title: boolean;
    /** Override mapping for status values */
    public status_overrides: HashMap<string>;
    /** Status of websocket */
    public websocket_connected: boolean;
    /** Options to use for the Timeline view at the bottom of the screen */
    public timeline_options: IPanelTimelineOptions;

    /** Current status of the active system */
    private _status: EnabledSystemStatus;

    private _theme: SpaceTheme | undefined;


    /**
     * When bookings loaded, triggers change.
     */
    private bookingsLoaded = new BehaviorSubject<HashMap[]>([]);

    public setSpace(space: Space) {
        // We have a new Space object. Angular will handle most of the propagation.
        // But first, we should hook up translations....
        const localeCode = space.locale_code;
        this._translate.use(localeCode);
        // ...and then we can assign this new Space

        this.space = space;
        this.timeline_options = {
            ...defaultBlockOptions,
            locale_code: localeCode
        };
        this._theme = this.space.theme_rbp;
    }

    /**
     * The text shown on screen to represent {@link _status}.
     *
     * Used for {@link isV1 V1} displays only - for V2+, prefer
     * {@link userFacingStatusText}. C.f. property of the same name in
     * <code>PanelDetailsDisplayV1Component</code>.
     */
    public get status_display(): Observable<string> {
        if (!this.websocket_connected && !this._service.setting('show_status_when_disconnected')) {
            return of(' ');
        }
        if (this.status_overrides && Object.keys(this.status_overrides).length === 1) {
            this._status = this.space.current ? 'unavailable' : Object.keys(this.status_overrides)[0] as any;
        }
        const permittedStatus: PermittedSystemStatus = (!this.hide_all ? this._status : null) || 'not-bookable';
        return of((this.status_overrides || [])[permittedStatus] || this._status);
    }

    /**
     * Current status of the system assigned to this {@link space}.
     *
     * If we do not have access to the server ({@link websocket_connected} is false),
     * or if we've been asked to obscure the current status ({@link hide_all} is
     * true), then this _may_ return a raw <code>null</code> value.
     *
     * @see updateStatus
     */
    public get status(): PermittedSystemStatus {
        if (!this.websocket_connected && !this._service.setting('show_status_when_disconnected')) {
            return 'not-bookable';
        }
        // Make '_status' incorporate any recent changes to 'space', 'hide_all', etc.
        this.updateStatus();
        // Perform any status overrides
        if (
            this.status_overrides &&
            Object.keys(this.status_overrides).length === 1 &&
            (this.space && !this.space.current || this._status !== 'unavailable')
        ) {
            this._status = Object.keys(this.status_overrides)[0] as any;
        }
        return this._status;
    }

    /**
     * Normalised representation of {@link status}.
     *
     * This method will provide the most nuance re: whether the room is
     * accepting new bookings.
     */
    public get normalisedStatus(): RoomDisplayStatus {
        // if we have not loaded rules, keep pending state.
        if (!this.rules) {
            return RoomDisplayStatus.Pending;
        }
        const currentStatus = this.status;
        if (!currentStatus) {
            // 'status' is either hidden or damaged somehow. Switch to the 'Pending' state.
            return RoomDisplayStatus.Pending;
        }
        switch (currentStatus) {
            case 'available':
                if (!this.rules.auto_approve) {
                    return RoomDisplayStatus.AvailableByRequest;
                }
                return RoomDisplayStatus.Available;
            case 'not-bookable':
                return RoomDisplayStatus.NotBookable;
            case 'pending':
                return RoomDisplayStatus.Pending;
            case 'unavailable':
                return RoomDisplayStatus.InUse;
        }
    }

    /**
     * User-friendly description of the room status.
     *
     * While this is usually based on {@link RoomDisplayStatus}, it _can_
     * be overridden by elements of {@link status_overrides}.
     */
    public get userFacingStatusText(): Observable<string> {
        const localStatus = this.status;
        const overrideDisplay = this.status_overrides && this.status_overrides[localStatus];
        if (overrideDisplay) {
            return of(overrideDisplay);
        }
        return this._translate.stream('status.' + this.normalisedStatus);
    }

    /** Whether booking details should be shown */
    public get show_details(): boolean {
        const theme = this.isV1();
        if (theme === undefined) {
            return false;
        }
        return this.websocket_connected || !!this._service.setting('show_status_when_disconnected');
    }

    /** Display string for the current time */
    public get display_time(): Observable<string> {
        return concat(
            of(this._translate.currentLang),
            this._translate.onLangChange.pipe(map(
                languageEvent => languageEvent.lang
            ))
        ).pipe(
            map(lang => {
                if (this.isV1() === undefined) {
                    return '';
                } else if (this.isV1()) {
                    return dayjs().format('h:mm A');
                } else if (is24HourTime(lang)) {
                    return dayjs().format('HH:mm');
                } else {
                    return dayjs().format('h:mma');
                }
            })
        );
    }

    constructor(public _service: ApplicationService, public _route: ActivatedRoute, public _translate: TranslateService) {
        super();
    }

    public ngOnInit(): void {
        // console.log('INNNNN system_id', this.system_id)
        // We want to prepare the translation service early on ('defaultLang' should
        // be described before we display any placeholder text)
        this._translate.addLangs(['en', 'fr-CA']);
        this._translate.setDefaultLang('en');

        this.subscription('app_ready', this._service.initialised.subscribe((is_ready) => {
            if (is_ready) {
                this.subscription('spaces_list_buildings', combineLatest([
                        this._route.paramMap.pipe(
                            mergeMap(params => {
                                this.system_id = params.get('system_id');
                                this._service.set('system', this.system_id);
                                return from(this._service.Spaces.show(this.system_id)).pipe(catchError(_ => of(undefined)))
                            }),
                        ),
                        this._service.Organisation.listen<Building[]>('buildings'),
                        this.bookingsLoaded
                    ]
                ).subscribe(([space, buildings, bookings]) => {
                    if (space && buildings.length > 0) {
                        // this requires a refactor, but essentially the rules will check for building
                        // levels. We need to listen for level loading to then load the rules.
                        // const foundSpace = spaces[0];

                        // As per MCK-480, these default options are based on the staff map default rules,
                        // except for the host "user". The RBP does not have a real user so, we do not
                        // evaluate rules for the user. This may lead to some inconsistencies when looking at
                        // booking panel vs the staff application. In future we should place defaults in a better spot.
                        this.rules = space.rulesFor({
                            date: dayjs().valueOf(),
                            duration: 30
                        });
                        this._service.log('Panel', 'Evaluated Rules', this.rules);
                        this.setSpace(new Space(
                            this._service.Spaces,
                            {
                                ...space,
                                bookings,
                                theme_rbp: this._theme
                            }
                        ));
                    }
                }));
                this.timeout('websocket', () => {
                    this.websocket_connected = true;
                });
                this.unsub('app_ready');
            }
        }));
    }

    public isV1(): boolean {
        return this._theme ? this._theme === SpaceTheme.v1 : undefined;
    }

    /**
     * Update the bookings for the active space
     * @param bookings Array of raw booking data
     */
    public updateBookings(bookings: HashMap[]): void {
        this.bookingsLoaded.next(bookings);
    }

    /**
     * Update the value of {@link _status}.
     *
     * Based on the current values of {@link hide_all}, {@link space},
     * {@link pending_timeout}, and one or two other properties, this function
     * changes {@link _status} to reflect whether or not the associated room is
     * open for new bookings.
     *
     * Invoke this only when a model (such as {@link cancel_timeout}) changes.
     * Most of the rest of the time you'd be better off either using the
     * accessor {@link status} (which itself invokes this when appropriate), or
     * querying {@link _status} directly.
     */
    public updateStatus(): void {
        if (this.hide_all) {
            this._status = null;
        } else {
            if (this.space && (this.space.current || this.space.next)) {
                const booking = this.space.current || this.space.next;
                this._status = this.space.current ? 'unavailable' : 'available';
                if (this.pending_timeout) {
                    const now = dayjs();
                    const date = dayjs(booking.date).startOf('m');
                    if (this.last_started >= now.valueOf() || this.last_started >= date.valueOf()) {
                        this._status = 'unavailable';
                    } else {
                        // Only meetings with durations less than 8 hours can be pending or cancelled
                        const pending_start = date.subtract(this.pending_timeout, 's');
                        const pending_end = date.add(this.cancel_timeout || this.pending_timeout, 's');
                        const end = date.add(booking.duration, 'm');
                        if (now.isAfter(pending_start, 's') && now.isBefore(pending_end, 's')) {
                            this._status = 'pending';
                        } else if (now.isAfter(pending_end, 's') && now.isBefore(end, 'm')) {
                            this._status = 'pending';
                            const business_start = dayjs(timeToDate(this.business_start || '08:00'));
                            const business_end = dayjs(timeToDate(this.business_end || '17:30'));
                            // Check if within business hours before cancelling the meeting
                            if (now.isBefore(business_start, 'm') || now.isAfter(business_end, 'm')) {
                                return;
                            }
                            this.endMeeting('timeout');
                        }
                    }
                }
            } else {
                this._status = 'available';
            }
        }
    }

    /**
     * Execute end meeting logic on engine driver
     * @param reason Reason for ending the meeting early
     */
    private endMeeting(reason: string = 'user_input') {
        if (this.space && this.can_cancel !== false) {
            const meeting = this.space.current || this.space.next;
            const module = this._service.Systems.get(this.space.id, 'Bookings');
            if (meeting.duration >= 8 * 60 && reason === 'timeout') {
                reason = 'hide multi-day';
            }
            if (meeting && module && meeting.date !== this.last_cancel) {
                const date = dayjs(meeting.date);
                module.execute('cancel_meeting', [meeting.date, reason]).then(
                    _ => {
                        this._service.Analytics.event(
                            'Checkin',
                            'cancelled',
                            `${this.space.id} at ${date.format('DD MMM YYYY, h:mm A Z')} | ${reason}`
                        );
                        this.last_cancel = meeting.date;
                    },
                    e => {
                        this._service.notifyError(`Error cancelling meeting. ${e}`);
                        this._service.Analytics.event(
                            'Checkin',
                            'cancel-failed',
                            `${this.space.id} at ${date.format('DD MMM YYYY, h:mm A Z')} | ${reason}`
                        );
                    }
                );
            }
        }
    }
}
